import React from 'react';
import './LbSquare.css';



const LbSquare = ({name, value, bgimage, onRouteChange, route}) => {
  return(

        <div className='LbSquare padding5 col-es-6 col-s-6 col-m-3'>
            <input className='LbSquare-input' type="radio" name={name} id={value} value={value} />
            <label className='LbSquare-label h100 w100' for={value} onClick={()=>onRouteChange(route)} style={{backgroundImage: `url(${bgimage})`}}></label>
        </div>

    
    );
}


export default LbSquare;
