import React from 'react';
import './GuidesB.css';


class GuidesB extends React.Component{
  //render
  render(){
    const { guides } = this.props;
    return(
        <div className='flxcenter GuidesB padding5 col-es-12 col-s-12'>
            <div className='col-es-0 col-s-1 col-m-2'></div>
            <div className='col-es-12 col-s-10 col-m-8'>
                <div className='GuidesBa col-es-12 col-s-12'>
                    <select id="experience" name="experience">
			            <option value="gold">Gold</option>
    			        <option value="silver">Silver</option>
	    		        <option value="bronze">Bronze</option>
		            </select>
		            <select id="language" name="language">
			            <option value="english">English</option>
			            <option value="swahili">Swahili</option>
    			        <option value="italian">Italian</option>
	    		        <option value="french">French</option>
		            </select>
		            <select id="safari" name="safari">
				        <option value="Masai Mara">Masai Mara</option>
		            </select>
                </div>
                <div className='GuidesBb col-es-12 col-s-12'>
                    {
                        guides.map(
                            (value,i)=>{
                                return (
                                    <div className='flxcenter col-es-12 col-s-12 border radius5 margin5'>
                                        <img className='h150 w150' src={guides[i].image} alt='ciao' />
                                        <table>
                                            <tr>
                                                <td>Nickname:</td>
                                                <td>{guides[i].nickname}</td>
                                                <td></td>
                                                <td>Experience:</td>
                                                <td>{guides[i].experience}</td>
                                                <td><button>book</button></td>
                                            </tr>
                                            <tr>
                                                <td>Language:</td>
                                                <td>{guides[i].language}</td>
                                                <td></td>
                                                <td>Safari:</td>
                                                <td>{guides[i].safari}</td>
                                            </tr>
                                        </table>
                                        
                                    </div>
                                );
                            }
                        )
                    }
                </div>
            </div>
            <div className='col-es-0 col-s-1 col-m-2'></div>
      </div>
    );
  }
}

export default GuidesB;