import React from 'react';
import './BookBd.css';
import LbRectangular from '../components/LbRectangular';
import tent1 from '../images/TENT1.png';
import tent2 from '../images/TENT2.png';
import tent3 from '../images/TENT3.png';
import room1 from '../images/ROOM1.png';
import room2 from '../images/ROOM2.png';

const BookBd = () => {
  return(  
    <div className='BookBd flxcenter col-es-12 col-s-12'>
        <div className="col-es-0 col-s-1 m-hidden">
        </div>
        <div className="BookBd-content col-es-12 col-s-10 col-m-12">
            <div className='BookBb-header padding15'>
                <p className='BookBb-title' > <span className="t1">Accomodation</span> <br /> Select your preferred Accomodation type or choose directly from the accomodations list </p>
            </div>
            <div className="flxcenter col-es-12 col-s-12" style={{padding:5}}>
                <div className="col-es-0 col-s-0 col-m-2">
                </div>
                <div className="flxcenter col-es-12 col-s-12 col-m-8">
                    <LbRectangular id={'tent1'} name={'Single Tents'} bgimage={tent1}/>
                    <LbRectangular id={'room1'} name={'Single Rooms'} bgimage={room1}/>
                    <LbRectangular id={'tent2'} name={'Double Tents'} bgimage={tent2}/>
                    <LbRectangular id={'room2'} name={'Double Rooms'} bgimage={room2}/>
                    <LbRectangular id={'tent3'} name={'3 Beds Tents'} bgimage={tent3}/>
                    <LbRectangular id={'room3'} name={'3 Beds Rooms'} bgimage={room2}/>                  
                </div>       
                <div className="col-es-0 col-s-0 col-m-2">
                </div>
            </div>     
        </div>
        <div className="col-es-0 col-s-1 m-hidden">
        </div>   
    </div>


  );
  }


export default BookBd;