import React from 'react';
import './LbRectangular.css';



const LbRectangular = ({id, name, bgimage}) => {
  return(

        <div className='LbRectangular padding5 col-es-6 col-s-6'>
            <div className='LbRectangular-label h150 maxw270 col-es-12 col-s-12' style={{backgroundImage: `url(${bgimage})`}}>
              <p className='t3'>{name}</p>
              <input id={id} className='LbRectangular-input radius5' list="numberofrooms" />
              <datalist id="numberofrooms">
                <option value="1" />
                <option value="2" />
                <option value="3" />
                <option value="4" />
                <option value="5" />
              </datalist>
            </div>
        </div>

    
    );
}


export default LbRectangular;