import React from 'react';
import './BookBb.css';
import LbSquare from '../components/LbSquare';
import stargold from '../images/STARGOLD.png';
import starsilver from '../images/STARSILVER.png';
import starbronze from '../images/STARBRONZE.png';
import guide from '../images/GUIDE.png';



const BookBb = ({text, text1, onRouteChange}) => {
  return(
<div className='BookBb flxcenter col-es-12 col-s-12'>
            <div className='col-es-0 col-s-2 m-hidden'></div>
            <div className='BookBb-content col-es-12 col-s-8 col-m-12'>
                <div className='BookBb-header padding15'>
                    <p className='BookBb-title' > <span className="t1">{text}</span> <br /> {text1} </p>
                </div>              
                <div className='flxcenter col-es-12 col-s-12'>
                    <div className='col-es-0 col-s-0 col-m-2'></div>
                    <div className='flxcenter col-es-12 col-s-12 col-m-8'>
                        <LbSquare name={'guideexperience'} value={'gold'} bgimage={stargold} route={''} onRouteChange={()=>{}}/>
                        <LbSquare name={'guideexperience'} value={'silver'} bgimage={starsilver} route={''} onRouteChange={()=>{}}/>
                        <LbSquare name={'guideexperience'} value={'bronze'} bgimage={starbronze} route={''} onRouteChange={()=>{}}/>
                        <LbSquare name={'guideexperience'} bgimage={guide} route={'guides'} onRouteChange={onRouteChange}/>          
                    </div>
                    <div className='col-es-0 col-s-0 col-m-2'></div>
                </div>
            </div>
            <div className='col-es-0 col-s-2 m-hidden'></div>
        </div>
    
    );
}


export default BookBb;